              fill="white"
            />
            <path
              d="M22.1188 0H1.88128C0.84221 0 0 0.84216 0 1.88128V22.1187C0 23.1578 0.84221 24 1.88128 24H22.1188C23.1578 24 24.0001 23.1578 24.0001 22.1187V1.88128C24 0.84216 23.1578 0 22.1188 0ZM22.4 22.1187C22.4 22.2738 22.2739 22.4 22.1188 22.4H1.88128C1.72619 22.4 1.60002 22.2738 1.60002 22.1187V1.88128C1.60002 1.72614 1.72619 1.59997 1.88128 1.59997H22.1188C22.2739 1.59997 22.4 1.72614 22.4 1.88128V22.1187H22.4Z"
