      <div class="row" v-for="(item, index) in listInfo" :key="index">
        <div class="col-md-1 col-lg-1 col-xl-1">
          <div class="circle" :style="{ backgroundColor: item.color }"></div>
        </div>
        <div class="col-md-5 col-lg-8 col-xl-8">
          <span>{{ item.title }}</span>
        </div>
        <div class="col-md-3 col-lg-3 col-xl-2 number">
          <div class="font-size1">{{ item.num1 }}</div>
          <div class="font-size2">{{ item.num2 }}</div>
